import {MessageBox} from 'mint-ui'
import { Toast } from 'mint-ui';

export default {
  showDownload: function ( message = '下载 幸福烘焙 后收藏这条内容' ) {
    MessageBox({
      title: '',
      message: message,
      showCancelButton: true,
      cancelButtonText: '取消',
      showConfirmButton: true,
      confirmButtonText: '确认'
    }).then(action => {
      if (action === 'confirm') {

      } else {

      }
    })
  },
  alert: function (msg, duration=2000) {
    Toast({
      message: msg,
      position: 'middle',
      duration: duration
    })
  }

}



