import api from '../api'

export default {
  getArticleDetail (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/detail/6/'+data, {}, successCallback, errCallback, isShowErrMsg)
  },
  getCourseDetail (data, successCallback, errCallback = function () {}, isShowErrMsg = true) {
    api.post('api/detail/11/'+data, {}, successCallback, errCallback, isShowErrMsg)
  }
}



